import React from 'react';
import LineChartWithSlider from './LineChartWithSlider';

function Empleo() {
  return (
    <LineChartWithSlider
        title="Desempleo, total (% de la fuerza laboral total) (estimacin modelada de la OIT)"
        fetchDataUrl="http://api.worldbank.org/v2/country/ESP/indicator/SL.UEM.TOTL.ZS?format=json"
        yAxisLabel="Tasa de Desempleo"
        formatYAxisValue={value => `${value.toFixed(0)}%`}
        description="El desempleo se refiere a la proporcin de la fuerza laboral que est sin trabajo pero disponible y buscndolo. Funte: Organizacin Internacional del Trabajo. Base de datos de estimaciones y proyecciones modeladas de la OIT ( ILOEST ) ILOSTAT."
    />
);
}

export default Empleo;
