import React from 'react';
import LineChartWithSlider from './LineChartWithSlider';

function GDPPerCapitaChart() {
    return (
        <LineChartWithSlider
            title="PIB per cpita, PPA ($ a precios internacionales actuales)"
            fetchDataUrl="http://api.worldbank.org/v2/country/ES/indicator/NY.GDP.PCAP.PP.CD?format=json"
            yAxisLabel="PIB per cpita, PPA ($ a precios internacionales actuales)"
            formatYAxisValue={value => `${value.toFixed(0)}`}
            description="Este indicador proporciona valores per cpita del producto interno bruto (PIB) expresado en dlares internacionales corrientes convertidos mediante el factor de conversin de paridad de poder adquisitivo (PPA). El PIB es la suma del valor agregado bruto de todos los productores residentes en el pas ms los impuestos sobre los productos y menos los subsidios no incluidos en el valor de los productos. El factor de conversin es un deflactor espacial de precios y un conversor de divisas que controla las diferencias de nivel de precios entre pases. La poblacin total es una poblacin de mitad de ao basada en la definicin de facto de poblacin, que cuenta a todos los residentes independientemente de su estatus legal o ciudadana. Fuente: Programa de Comparacin Internacional, Banco Mundial | Base de datos de indicadores de desarrollo mundial, Banco Mundial | Programa PPP Eurostat-OCDE."
        />
    );
}

export default GDPPerCapitaChart;
