import React from 'react';
import LineChartWithSlider from './LineChartWithSlider';

function EducationExpenditureChart() {
    return (
        <LineChartWithSlider
            title="Gasto pblico en educacin, total (% del gasto del gobierno)"
            fetchDataUrl="http://api.worldbank.org/v2/country/ES/indicator/SE.XPD.TOTL.GB.ZS?format=json"
            yAxisLabel="Gasto pblico en educacin, total (% del gasto del gobierno)"
            formatYAxisValue={value => `${value.toFixed(0)}%`}
            description="El gasto pblico en educacin como porcentaje del gasto total del Gobierno corresponde al gasto pblico total (corriente y de capital) en educacin, expresado como porcentaje del gasto total del Gobierno en todos los sectores en un ao financiero determinado. El gasto pblico en educacin incluye el gasto del Gobierno en instituciones educativas (pblicas y privadas), administracin educativa y subsidios para entidades privadas (estudiantes/hogares y otras entidades privadas). Fuente: Instituto de Estadstica de la Organizacin de las Naciones Unidas para la Educacin, la Ciencia y la Cultura ( UNESCO )."
        />
    );
}

export default EducationExpenditureChart;
