import React from 'react';
import LineChartWithSlider from './LineChartWithSlider';

function InflationChart() {
    return (
        <LineChartWithSlider
            title="Inflacin, precios al consumidor (% anual)"
            fetchDataUrl="http://api.worldbank.org/v2/country/ESP/indicator/FP.CPI.TOTL.ZG?format=json"
            yAxisLabel="Porcentaje de inflacin"
            formatYAxisValue={value => `${value.toFixed(0)}%`}
            description="La inflacin, medida por el ndice de precios al consumidor, refleja el cambio porcentual anual en el costo para el consumidor promedio de adquirir una canasta de bienes y servicios que puede fijarse o cambiarse en intervalos especficos, como por ejemplo anualmente. Generalmente se utiliza la frmula de Laspeyres. Fuente: Fondo Monetario Internacional, Estadsticas Financieras Internacionales y archivos de datos."
        />
    );
}

export default InflationChart;
